import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ModalController } from 'ionic-angular';
import { LoadingController } from 'ionic-angular';
import { Events } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
import { ViewController } from 'ionic-angular';
import { FormBuilder, FormGroup, Validators, AbstractControl } from '@angular/forms';

/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */


@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  authForm: FormGroup;


  constructor(public navCtrl: NavController, public navParams: NavParams, public modalCtrl: ModalController,public formBuilder: FormBuilder, public loadingCtrl: LoadingController,public events: Events, public alertCtrl: AlertController, public viewCtrl : ViewController) {



        this.authForm = formBuilder.group({
            username: ['', Validators.compose([Validators.required, Validators.pattern('[a-zA-Z]*'), Validators.minLength(8), Validators.maxLength(30)])],
            password: ['', Validators.compose([Validators.required, Validators.minLength(8)])]
        });



  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');
  }





public closeModal(){
 //	this.events.publish('modalServices');
    this.viewCtrl.dismiss();
}





}



